import React, { useState, useContext } from 'react'
import UserContext from '../../../../../context/UserContext'
import { Form, Button, FloatingLabel } from 'react-bootstrap'
import ReviewsService from '../../../../../services/reviews.service'

const reviewsService = new ReviewsService()

const NewReviewForm = (props) => {
  const { loggedUser, showText } = useContext(UserContext)
  const refTeacher = props.teacherId
  const today = new Date()

  const [formData, setFormData] = useState({
    creator: loggedUser._id,
    ref: refTeacher,
    comment: '',
    date: today,
    rating: '',
  })

  const handleChange = (e) => {
    let { name, value } = e.currentTarget

    setFormData({ ...formData, [name]: value })
  }

  const handleSubmit = (e) => {
    e.preventDefault()

    reviewsService
      .createReview(formData)
      .then((res) => {
        props.closeForm()
        props.refreshReviews()
      })
      .catch((err) => showText(err.response.data.message))
  }

  return (
    <Form onSubmit={handleSubmit}>
      <FloatingLabel controlId="comment" label="Comentarios">
        <Form.Control
          as="textarea"
          placeholder="Deja un comentario sobre el profesor..."
          style={{ height: '100px' }}
          name="comment"
          type="text"
          onChange={handleChange}
          value={formData.comment}
          maxLength="90"
        />
      </FloatingLabel>

      <Form.Group className="mb-3" controlId="rating">
        <Form.Label>Puntuaci√≥n</Form.Label>
        <Form.Select
          name="rating"
          type="number"
          onChange={handleChange}
          value={formData.rating}
        >
          <option>Puntuaci√≥n</option>
          <option value="1">‚òÖ</option>
          <option value="2">‚òÖ‚òÖ</option>
          <option value="3">‚òÖ‚òÖ‚òÖ</option>
          <option value="4">‚òÖ‚òÖ‚òÖ‚òÖ</option>
          <option value="5">‚òÖ‚òÖ‚òÖ‚òÖ‚òÖ</option>
        </Form.Select>
      </Form.Group>

      <Button className="btn-create-fit" variant="primary" type="submit">
        A√±adir
      </Button>
    </Form>
  )
}

export default NewReviewForm
